import React from 'react';
import axios from 'axios';

import Spinner from '../common/Spinner';

export default class ContactForm extends React.Component {
  constructor() {
    super();

    this.state = {
      isSubmitting: false,
      name: '',
      email: '',
      subject: '',
      message: ''
    };

    this.handleInputChange = this.handleInputChange.bind(this);
  }

  handleSubmitForm = event => {
    event.preventDefault();
    if (this.state.isSubmitting) {
      return;
    }

    this.setState({
      isSubmitting: true
    });

    axios
      .post(`http://localhost:5001/resume-manav-chawla/us-central1/sendMail`)
      .then(res => {
        console.log(res);
      })
      .catch(err => {
        console.log('here');
        console.log(err);
      })
      .finally(() => {
        this.resetFormState();
      });
  };

  resetFormState() {
    this.setState({
      isSubmitting: false,
      name: '',
      email: '',
      subject: '',
      message: ''
    });
  }

  handleInputChange(event) {
    const target = event.target;
    const value = target.value;
    const name = target.name;
    this.setState({
      [name]: value
    });
  }

  render() {
    return (
      <form onSubmit={this.handleSubmitForm}>
        <div className="d-flex">
          <div className="form-group flex-grow-1">
            <input
              type="text"
              className="form-control"
              name="name"
              placeholder="Name"
              onChange={this.handleInputChange}
            />
          </div>
          <div className="form-group ml-3 flex-grow-1">
            <input
              type="email"
              className="form-control"
              name="email"
              placeholder="Email"
              onChange={this.handleInputChange}
            />
          </div>
        </div>
        <div className="form-group mt-3">
          <input
            type="text"
            className="form-control"
            name="subject"
            placeholder="Subject"
            onChange={this.handleInputChange}
          />
        </div>
        <div className="form-group mt-3">
          <textarea
            class="form-control"
            name="message"
            placeholder="Message"
            rows="5"
            onChange={this.handleInputChange}
          />
        </div>
        <div className="d-flex mt-3 flex-row-reverse">
          <button
            type="submit"
            className={
              'btn btn-primary align-items-end ' +
              (this.state.isSubmitting ? 'disabled' : '')
            }
          >
            {this.state.isSubmitting ? <Spinner></Spinner> : <></>}
            Send Message
          </button>
        </div>
      </form>
    );
  }
}
